<template>
  <b-container>
    <loading :active.sync="Loading" :can-cancel="true" :is-full-page="fullPage"></loading>
    <b-card>
      <b-row>
        <b-col cols="6" align-self="center">
          <h4>餐廳列表</h4>
        </b-col>
        <b-col cols="6" align-self="center">
          <span class="float-right">
            <b-button @click="showForm" variant="success" size="sm">新增餐廳</b-button>
          </span>
        </b-col>
      </b-row>
      <b-form-row>
        <b-table responsive striped hover :items="items" :fields="fields">
          <!-- 操作選單按鈕 -->
          <template slot="actions" slot-scope="row">
            <b-dropdown id="ddown-right" right text="選項" size="sm" variant="primary">
              <b-dropdown-item @click.stop="showUpdateForm(row.item, $event.target)">修改</b-dropdown-item>
              <b-dropdown-item @click="deleteRes(row.item.id)">刪除</b-dropdown-item>
            </b-dropdown>
          </template>
          <!-- /操作選單按鈕 -->
        </b-table>
      </b-form-row>
    </b-card>
    <!-- 新增餐廳表格 -->
    <b-modal id="resForm" title="新增餐廳資訊" ref="resFormRef" hide-footer>
      <b-form>
        <b-form-group label="餐廳名稱:">
          <b-form-input type="text" v-model="form.name" placeholder="輸入餐廳名稱" required></b-form-input>
        </b-form-group>
        <b-form-group label="地址:">
          <b-form-input type="text" v-model="form.address" placeholder="輸入餐廳地址" required></b-form-input>
        </b-form-group>
        <b-form-group label="類型:">
          <b-form-input type="text" v-model="form.food_types" placeholder="輸入餐廳類型" required></b-form-input>
        </b-form-group>
        <b-form-group label="造訪時間:">
          <b-form-input type="date" v-model="form.visit_time" placeholder="輸入造訪時間" required></b-form-input>
        </b-form-group>
        <b-form-group label="造訪目的:">
          <b-form-input type="text" v-model="form.purpose" placeholder="輸入造訪目的" required></b-form-input>
        </b-form-group>
        <b-form-group label="短評:">
          <b-form-input type="text" v-model="form.short_comment" placeholder="輸入短評" required></b-form-input>
        </b-form-group>
        <span class="float-right">
          <b-button type="reset" variant="danger" @click="onReset()">清除</b-button>
          <b-button type="createRes" variant="primary" @click="createRes()">送出</b-button>
        </span>
      </b-form>
    </b-modal>
    <!-- /新增餐廳表格 -->
    <!-- 修改既有餐廳表格 -->
    <b-modal id="updatedForm" title="修改餐廳資訊" hide-footer>
      <b-form>
        <b-form-group label="餐廳名稱:">
          <b-form-input type="text" v-model=" updatedForm.name" placeholder="輸入餐廳名稱" required></b-form-input>
        </b-form-group>
        <b-form-group label="地址:">
          <b-form-input type="text" v-model=" updatedForm.address" placeholder="輸入餐廳地址" required></b-form-input>
        </b-form-group>
        <b-form-group label="類型:">
          <b-form-input type="text" v-model=" updatedForm.food_types" placeholder="輸入餐廳類型" required></b-form-input>
        </b-form-group>
        <b-form-group label="造訪時間:">
          <b-form-input type="date" v-model=" updatedForm.visit_time" placeholder="輸入造訪時間" required></b-form-input>
        </b-form-group>
        <b-form-group label="造訪目的:">
          <b-form-input type="text" v-model=" updatedForm.purpose" placeholder="輸入造訪目的" required></b-form-input>
        </b-form-group>
        <b-form-group label="短評:">
          <b-form-input
            type="text"
            v-model=" updatedForm.short_comment"
            placeholder="輸入短評"
            required
          ></b-form-input>
        </b-form-group>
        <span class="float-right">
          <b-button type="updateRes" variant="primary" @click="updateRes(updatedForm)">送出</b-button>
        </span>
      </b-form>
    </b-modal>
    <!-- /修改既有餐廳表格 -->
    <!-- Modal Component -->
    <!-- <b-modal id="modal1" title="刪除餐廳資訊" hide-footer>
      <p class="my-4">確定要刪除餐廳資訊嗎?</p>
      <span class="float-right">
        <b-button type="createRes" variant="danger" @click="deleteRes(row.items.id)">確定</b-button>
      </span>
    </b-modal>-->
  </b-container>
</template>

<script>
// Import component
import Loading from "vue-loading-overlay";
// Import stylesheet
import "vue-loading-overlay/dist/vue-loading.css";
export default {
  name: "ResTable",
  data() {
    return {
      Loading: false,
      fullPage: true,
      // 定義顯示資料表欄位
      fields: {
        name: {
          label: "餐廳",
          sortable: true
        },
        address: {
          label: "地址",
          sortable: true
        },
        food_types: {
          label: "類型",
          sortable: true
        },
        visit_time: {
          label: "造訪時間",
          sortable: true
        },
        purpose: {
          label: "造訪目的",
          sortable: true
        },
        short_comment: {
          label: "短評",
          sortable: true
        },
        actions: {
          label: "操作",
          sortable: false
        }
      },
      // 儲存要顯示的餐廳資料表欄位內容
      items: [],
      // 儲存要送到後端的餐廳資料表欄位內容
      form: {
        name: "",
        address: "",
        food_types: "",
        visit_time: "",
        purpose: "",
        short_comment: ""
      },
      // 儲存要送到後端的餐廳資料表欄位更新內容
      updatedForm: []
    };
  },
  components: {
    Loading
  },
  // 預設顯示所有餐廳清單
  mounted() {
    this.listRes();
  },
  methods: {
    // 顯示所有餐廳資料
    listRes() {
      this.Loading = true;
      this.$axios
        .get("/api/restaurants/list/")
        .then(response => {
          this.items = response.data;
          this.Loading = false;
        })
        .catch(error => {
          this.Loading = false;
          console.log(error);
        });
    },
    // 創建餐廳資料
    createRes() {
      const payload = {
        name: this.form.name,
        address: this.form.address,
        food_types: this.form.food_types,
        visit_time: this.form.visit_time,
        purpose: this.form.purpose,
        short_comment: this.form.short_comment
      };
      this.Loading = true;
      this.$axios
        .post("/api/restaurants/create/", payload)
        .then(response => {
          this.$refs.resFormRef.hide();
          this.Loading = false;
          this.listRes();
        })
        .catch(err => {
          this.Loading = false;
          console.log(err);
        });
    },
    // 更新餐廳資料
    updateRes(updatedForm) {
      this.Loading = true;
      this.$axios
        .put("/api/restaurants/update/" + updatedForm.id + "/", updatedForm)
        .then(response => {
          this.Loading = false;
          // this.items = response.data;
          this.listRes();
        })
        .then(this.hideUpdateForm())
        .catch(err => {
          this.Loading = false;
          console.log(err);
        });
    },
    // 刪除餐廳資料
    deleteRes(id) {
      this.Loading = true;
      this.$axios
        .delete("/api/restaurants/delete/" + id + "/")
        .then(response => {
          this.Loading = false;
          this.listRes();
        })
        .catch(err => {
          this.Loading = false;
          console.log(err);
        });
    },
    // 顯示新增餐廳表單
    showForm() {
      this.$refs.resFormRef.show();
    },
    // 清除新增餐廳表單
    onReset() {
      /* Reset our form values */
      this.form.name = "";
      this.form.address = "";
      this.form.food_types = "";
      this.form.visit_time = "";
      this.form.purpose = "";
      this.form.short_comment = "";
      /* Trick to reset/clear native browser form validation state */
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      });
    },
    // 顯示修改餐廳表單
    showUpdateForm(item, index, button) {
      this.updatedForm = item;
      this.$root.$emit("bv::show::modal", "updatedForm", button);
    },
    // 隱藏餐廳表單
    hideUpdateForm() {
      this.$root.$emit("bv::hide::modal", "updatedForm");
    }
  }
};
</script>


